
























const Glide= require('vue-glide-js')
const GlideSlide= require('vue-glide-js')
import { Component, Vue } from 'vue-property-decorator'

interface Options {
  gap :Number,
  type: string,
  autoplay: number,
  rewind: boolean,
  bound: boolean,
  perView?: number,
  breakpoints?: {},
}

@Component({
  components: {
	[Glide.name]: Glide,
	[GlideSlide.name]: GlideSlide,
  }}) 

export default class MainView extends Vue {
  private srcArr: Array<string> = ["drapak2.jpg","drapak3.jpg","drapak5.jpg", "drapak8.jpg", "drapak6.jpg", "drapak7.jpg", "drapak9.jpg", "drapak10.jpg" ]
  private options: Options = {
    gap: 0,
    type: 'carousel',
    autoplay: 4000,
    rewind: true,
    bound: true,
    breakpoints: {
      1184: {
      perView: 1,
      }, 
    }
  }
  public changeToGallery() {
	  this.$emit('changeToGallery')
  }
}
